import React from 'react';
import moment from 'moment'; 
import 'react-dates/initialize';
import { SingleDatePicker } from 'react-dates';



export default class ExpenseForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            description: props.expense ? props.expense.description : '',
            note: props.expense ? props.expense.note : '',
            amount: props.expense ? (props.expense.amount / 100).toString() : '',
            createdAt: props.expense ? moment(props.expense.createdAt) : moment(),
            calenderFocused: false,
            error: undefined,
            buttonText: props.expense ? 'Edit Expense' :'Add Expense'
        };
    }
   
    onDescriptionChange = (e) => {
        const description = e.target.value;
        this.setState(()=>{
            return {
                description
            };
        });
    };
    onNoteChange = (e) => {
        const note = e.target.value;
        this.setState(()=>{
            return {
                note
            }
       }); 

    };
    onAmountChange = (e) => {
        const amount = e.target.value;
        // ^\d*(\.\d{0,2})?$
        if(!amount || amount.match(/^\d{1,}(\.\d{0,2})?$/)) {
            this.setState(()=>{
                return {
                    amount
                }
            });
        } 

    };
    onDateChange = (createdAt) => {
        if (createdAt) {
            this.setState(()=>{
                return {
                    createdAt
                };
            });
        }
    };
    onFocusChange = ({ focused}) => {
        this.setState(()=>{
            return {
                calendarFocused: focused
            }
        });
    };
    onSubmit = (e) =>{
        e.preventDefault();
        if (!this.state.description || !this.state.amount) {
            // set error state equal to 'Please Provide Description and Amount.
            this.setState(()=>{
                return {
                    error: 'Please provide value into Description and Amount.'
                };
            });
        } else {
            // Clear the error
            console.log('submitted with no errors');
            this.setState(()=>{
                return {
                    error: ''
                };
            });
            this.props.onSubmit({
                description: this.state.description,
                amount: parseFloat(this.state.amount, 10) * 100,
                createdAt: this.state.createdAt.valueOf(),
                note: this.state.note
                
            });
        }

    }
    render() {
        return (
        <div>
            <form onSubmit={this.onSubmit}>
                <input type="text" placeholder="Description Here..." autoFocus value={this.state.description} onChange={this.onDescriptionChange}/>
                <input type="text" placeholder="Set the Amount..." value={this.state.amount} onChange={this.onAmountChange} />
                <SingleDatePicker 
                isOutsideRange={()=> false}
                numberOfMonths={1} date={this.state.createdAt} 
                onDateChange={this.onDateChange} 
                focused={this.state.calendarFocused} 
                onFocusChange={this.onFocusChange}
                id="your_unique_id"
                showClearDate={true}
                />
                <textarea placeholder="Add a note for your expense (optional)" value={this.state.note} onChange={this.onNoteChange}>
                </textarea>
                {this.state.error && <p>{this.state.error}</p>}
                <button>{this.state.buttonText}</button>
            </form>
        </div>
        );
    }
};
